import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import './home.scss';
import header_bg from '../images/header.jpg';
import bird from '../images/bird.png';
import req from '../io/req';

function Home() {
  const [data, setData] = useState({});



  // const title = '2019企鹅跑-王者快跑';
  const [flag, setFlag] = useState(0);
  const [left, setLeft] = useState(0);
  const [active, setActive] = useState(0);
  const headers = data.matchs || [];

  useEffect(() => {
    req('get')('/combos?', {
      categoryId: 1
    }).then(data => {
      setData(data);
    });
  }, []);

  function touchStart(e) {
    setFlag(e.touches[0].clientX);
  }
  function change(e) {
    setFlag(e.touches[0].clientX);
    let move = e.touches[0].clientX - flag;
    if (move + left > 0) {
      return setLeft(0);
    }
    if (move + left < window.screen.availWidth-(headers.length * 108 + 16)) {
      return setLeft(window.screen.availWidth-(headers.length * 108 + 16));
    }
    return setLeft(move + left);
  }

  function activeItem(e, num) {
    setActive(num);
    const { left, right } = e.target.getBoundingClientRect();
    if (left < 0) {
      setLeft(-108 * num);
    }
    if (right > window.screen.availWidth) {
      setLeft(window.screen.availWidth - 16 -(num+1)*108);
    }
  }

  const item = (header, num) => <div
    key={num}
    onClick={e => activeItem(e, num)}
    className={`item ${ num === active ? 'activity' : ''}`}
  >
    <div className="item-content">
      <div className="pic">
        <img className='city' src={header.picture} alt=""/>
        <div className="activity-content">
          <span>{header.name}</span>
        </div>
      </div>
      <div className='city-content city-name'>{header.name}</div>
      <div className="city-content city-time">{header.startTime}</div>
    </div>
  </div>;
  const items = headers.map(item);

  // if (data.matchs && data.matchs.length > 0) {
  //   msg = data.matchs[active].startTime + '  ' + data.matchs[active].categoryName;
  // }
  // console.log('time', data)

  return(
    <div className='p-home'>
      <div className='c-head'>
        <div className="bg">
          <img className='bg_img' src={data.bannerPic} alt="banner_bg"/>
        </div>
        <div className="content">
          <div className="title">{data.name}</div>

          <div className="chain-box">
            <div
              className="chains"
              style={{
                transform: `translateX(${left}px)`,
                width: (headers.length * 108 + 16) + 'px'
              }}
              onTouchStart={touchStart}
              onTouchMove={change}
            >
              {items}
            </div>
          </div>
        </div>
      </div>
      <Body
        list={data.matchs ? data.matchs[active].combos : []}
        time={data.matchs ? data.matchs[active].startTime : ''}
        end={data.matchs ? data.matchs[active].endTime : ''}
        banner={data.bannerPic}
        place={data.matchs ? data.matchs[active].categoryName : ''}
      />
    </div>
  );
};

const Body = React.memo(props => {
  const ul = useRef(null);
  const { list } = props;
  // console.log(list[0]);
  const item = (item, index) => {
    const query = {
      title: item.name,
      date: props.time,
      end: props.end,
      place: props.place,
      isBird: item.hasDiscount === 1,
      price: item.price,
      discountPrice: item.discountPrice,
      banner: props.banner,
      hasChild: item.childNum > 0,
      childNum: item.childNum,
      adultNum: item.adultNum,
      openPop: false,
      id: item.id
    }
    const dis = item.hasDiscount === 1;
    return <li className="item" key={index + Math.random(new Date())}>
      <div className="preview">
        <img src={item.picture} alt="" className="pre-pic"/>
      { dis ? <img src={bird} alt="早鸟价" className="bird"/> : null }
      </div>
      <div className="infos">
        <div className="ticket-title">{item.name}</div>
        <div className="ticket-intro">{props.time+'  '+props.place}</div>
        <p className="ticket-price">
          <span className="red-price">{dis ? item.discountPrice.toFixed(2) : item.price.toFixed(2)}</span>
          {dis ? <span className="old-price">¥{item.price.toFixed(2)}</span> : null}
        </p>
        <div className="btn">
          <Link to={{
            pathname: '/signup',
            query
          }}>
            查看
          </Link>
        </div>
        <div className="bottom-line"/>
      </div>
    </li>
  };

  useEffect(() => {
    ul.current.scrollIntoView();
  }, [props.list]);

  return(
    <div className="c-body">
      <div className="content">
        <div className="title">选择套餐</div>
        <div className="list">
          <ul ref={ul}>{list.map(item)}</ul>
        </div>
      </div>
    </div>
  );
});

export default Home;
