function SignUp(props) {
  const [ticket, setTicket] = useState({});
  const [players, setPlayers] = useState({});
  const [price, setPrice] = useState(0);
  useEffect(() => {
    setTicket(props.location.query || {});
    const config = {
      token: '123'
    };
    if (props.location.query) {
      const prop = props.location.query;
      if (prop.isBird) {
        setPrice(prop.discountPrice);
      } else {
        setPrice(prop.price);
      }
      console.log(props.location.query)
      // setPrice
    }
    if (props.location.query.jia) {
      req('get')(props.location.query.jia, config)
      .then(setPlayers);
      return;
    }

    req('get')('/user/exists', config)
      .then(setPlayers);
  }, []);
  return(
    <div className='p-sign-up'>
      <div className="header">
        <div className="bg-layout">
          <img className='bg-img' src={ticket ? ticket.banner: ''} alt=""/>
        </div>
        <div className="bg">
          <TicketHead {...ticket} />
        </div>
      </div>
      <Players
        {...players}
        childNum={ticket.childNum}
        adultNum={ticket.adultNum}
        hasChild={ticket.hasChild}
        editable={true}
        openPop={ticket.openPop}
        oldProp={{
          ...props.location.query
        }}
        jia='/jia1'
      />
      <div className="br"/>
      <Payment price={price} ticket={ticket} players={jiaPlayers}/>
    </div>
  );
}

function Payment(props) {
  console.log(props.price)
  const [promoCode, setPromoCode] = useState('');
  const [price, setPrice] = useState(props.price);
  const prompt = Modal.prompt;

  useEffect(() => {
    setPrice(props.price);
  }, [props.price]);

  function checkPromo(promo) {
    if (promo.toLowerCase() === 'qep') {
      setPromoCode(promo);
      setPrice(price*0.88);
      return Toast.info('兑换成功', 0.618);
    }
    return Toast.info('优惠码错误', 0.618);
  }

  function inputPromo() {
    prompt('请输入优惠码', null, [
      { text: '取消' },
      {
        text: '确定',
        onPress: code => checkPromo(code)
      }
    ]);
  }

  return(
    <div className='payment'>
      <div className="line-item">
        <div className="label">优惠码</div>
        { promoCode
          ? <div className='label-content'>{ promoCode }</div>
          : <>
            <div
              className="preferential"
              onClick={inputPromo}
            >请输入优惠码</div>
            <div className="arrow"></div>
          </>
        }
      </div>
      <div className="line-item">
        <div className="label">支付方式</div>
        <div className="label-content">微信支付</div>
      </div>
      <div className="pay-bottom">
        <p className='pay-price'>
          <span className="pay-label">总价</span>
          <span className="red-price">{price.toFixed(2)}</span>
        </p>
        <div className="pay-btn" onClick={() => console.log('need to pay')}>
          <Link to={{
            pathname: '/success',
            query: {
              ticket: props.ticket,
              players: props.players,
              price
            }
          }}>去支付</Link>
        </div>
      </div>
    </div>
  );
}

export default SignUp;
