import axios from 'axios';

const baseUrl = 'https://tmock.qq.com/mock/342/h5';

const req = method => (url, data={}) => {
  let config = {};
  method = method.toLowerCase() || 'get';
  if (method === 'get') {
    config = {
      params: {
        ...data
      }
    }
  }
  if (method === 'post') {
    config = {
      ...data
    }
  }
  return axios[method](baseUrl+url, config)
    .then(res => {
      if (res.status === 200 && res.data && Number(res.data.code) === 200) {
        return res.data.data;
      }
      return res;
    })
    .catch(console.log);
}

export default req;
