function idProtect(id='') {
  const reg = /(\d{6})\d{6}(\d{6})/;
  return id.replace(reg, '$1******$2');
}

function handlePlayer(id) {
}

function showPlayers(players) {
  const playersLists = players.map(
    (player, i) => <div
      className="line-item player-info"
      key={i}
    >
      <div className="label">选手{i + 1}</div>
      <div
        className="player-body"
        onClick={handlePlayer.bind(null, player.id)}
      >
        <div className="player-name">{player.name}</div>
        <div className="player-id">{idProtect(player.credentialsNum)} {player.getBagType}</div>
      </div>
      <div className="arrow" />
    </div>
  );
  return playersLists;
}

function label(tag) {
  return(
    <div className="line-item">
      <div className="label">{tag}选手</div>
    </div>
  );
}

function Players(props={}) {
  const { adult=[], children=[], hasChild, editable, childNum, adultNum } = props;
  const [popType, setPopType] = useState('成人');
  const [childArr, setChildArr] = useState(children);
  const [adultArr, setAdultArr] = useState(adult);
  const [childSelected, setChildSelected] = useState([]);
  const [adultSelected, setAdultSelected] = useState([]);

  let flag = false;
  if (props.oldProp) {
    flag = props.openPop || props.oldProp.openPop || false;
  }
  const [show, setShow] = useState(flag);

  let popArr = adultArr;
  let popArrSeter = setAdultArr;
  if (popType === '儿童') {
    popArr = childArr;
    popArrSeter = setChildArr;
  }

  useEffect(() => {
    // 修复滑动穿透的问题
    document.querySelector('.pop').addEventListener("touchmove", (e) => {
      // 执行滚动回调
      e.preventDefault();
      e.stopPropagation();
    }, {
      passive: false //  禁止 passive 效果
    })
  }, []);

  useEffect(() => {
    setAdultArr(props.adult || []);
    setChildArr(props.children || []);

    if (!editable) {
      setAdultSelected(adult);
    }
  }, [props.adult, props.children]);

  function handleBtn(type) {
    setPopType(type);
    openPop();
  }

  function addPlayer(type, players) {
    let left = adultNum;
    if (type === '儿童') {
      left = childNum;
    }
    // 剩余人数
    const last = left - players.length;

    const editLine = <div className="players line-item">
      还需添加
      <span className={last === 0 ? '' : 'player-num'}>{last}个</span>
      {type}
      <div className="btn-box">
        <div
          className='add-player'
          onClick={() => handleBtn(type)}
        >{last === 0 ? '编辑' : '添加'}</div>
      </div>
    </div>;

    function handleClick() {
      if (type === '成人') {
        openPop();
      }
    }

    return(
      <>
        {editable && editLine}
        {showPlayers(players)}
      </>
    );
  }

  function openPop() {
    setShow(true);
  }

  function closePop() {
    if (popType === '成人') {
      popArrSeter(adultArr);
    }
    if (popType === '儿童') {
      popArrSeter(childArr);
    }
    setShow(false);
  }

  function chargePlayerInfo(id) {
  }

  function showPopPlayer(players) {
    const list = players.map((player, index) => {
      const blue = shouldAdd(player);
      const newPlayer = {...player, blue};
      return(
        <div key={index} className='pop-player'>
          <div className="pop-edit"/>
          <div className="pop-player-info">
            <p className="pop-name">{player.name}</p>
            { blue ? <Link to={{
              pathname: '/input',
              query: {
                type: 'add',
                oldProp: {
                  ...props.oldProp,
                  openPop: true
                },
                jia: '/jia3'
              }
            }}><p className="pop-info should-add" onClick={() => chargePlayerInfo(player.id)}>信息不全，点击补充</p></Link>
                : <p className="pop-info">{idProtect(player.credentialsNum)}</p>
            }
          </div>
          <div
            className={`pop-check${player.selected ? ' checked' : ''}`}
            onClick={() => handleSelect(newPlayer)}
          />
        </div>
      );
    });
    return(list);
  }

  function shouldAdd(player) {
    const key = Object.keys(player);
    for (let i=0, len=key.length; i < len; i++) {
      if (key[i] === 'selected' || key[i] === 'blue') {
        continue;
      }
      if (!player[key[i]]) {
        return true;
      }
    }
    return false;
  }

  function handleSelect(item) {
    const arr = [];
    let limit = popType === '儿童' ? childNum : adultNum;
    limit = limit - popArr.filter(player => player.selected).length;
    popArr.forEach(player => {
      if (!item.blue && player.name === item.name) {
        if ((limit === 0 && player.selected) || limit > 0) {
          player.selected = !player.selected;
        }
      }
      arr.push({...player});
    });
    popArrSeter(arr);
  }

  function submitSelect() {
    if (popType === '儿童') {
      let arr = childArr.filter(child => child.selected)
      setChildSelected(arr);
    }
    if (popType === '成人') {
      let arr = adultArr.filter(adult => adult.selected);
      setAdultSelected(arr);
    }
    closePop();
  }

  let playerLeft = 0;
  const selectedNum = popArr.filter(player => player.selected).length;
  if (popType === '儿童') {
    playerLeft = Number(childNum) - selectedNum;
  }
  if (popType === '成人') {
    playerLeft = Number(adultNum) - selectedNum;
  }

  return(
    <div className='c-players'>
      <div className="yellow-title">选手信息</div>
      {hasChild && label('成人')}
      {addPlayer('成人', adultSelected)}
      {hasChild && label('儿童')}
      {hasChild && addPlayer('儿童', childSelected)}
      <div className={`pop ${show ? 'show-pop' : ''}`}>
        <div className="pop-box">
          <div className="pop-content">
            <div className="pop-title">
              添加选手信息
              <div
                className="close-btn"
                onClick={closePop}
              />
            </div>
            <Link to={{
              pathname: '/input',
              query: {
                type: 'new',
                oldProp: {
                  ...props.oldProp,
                  openPop: true
                },
                jia: props.jia
              }
            }}>
              <div className="pop-add">
                新增选手
                <div className="arrow"/>
              </div>
            </Link>
            {showPopPlayer(popArr)}
          </div>
          <div className="pop-btn bottom-box">
            { playerLeft > 0
                ? <div className="btn btn-disable">还需添加{playerLeft}个选手</div>
                : <div className="btn" onClick={submitSelect}>完成</div>
            }
          </div>
        </div>
      </div>
    </div>
  );
}

export default Players;
